import React, { useState, useEffect } from 'react';
import {
  Grid,
  Container,
  Modal,
  Button,
  Header,
  Segment,
  Form,
  Checkbox,
  Divider,
  Loader,
  Label as ErrorLabel,
  Dropdown,
  Icon,
} from 'semantic-ui-react';
import { Label } from '../Nav/NavTheme';
import TextareaAutosize from 'react-textarea-autosize';
import { useForm } from 'react-hook-form';
import userService from '../../services/user';
import { useDispatch } from 'react-redux';
import { updateDepSemInfo } from '../../reducers/userReducer';

const UserQuotaPopup = () => {
  const [isOpen, setIsOpen] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isNotification, setIsNotification] = useState(true);
  const [isEmail, setIsEmail] = useState(false);
  const [selectedDeps, setSelectedDeps] = useState([]);
  const [selectedSemester, setSelectedSemester] = useState();
  const [submitReady, setSubmitReady] = useState(false);
  const dispatch = useDispatch();
  useEffect(() => {
    if (
      selectedSemester &&
      selectedDeps.length > 0 &&
      selectedDeps.length < 3
    ) {
      setSubmitReady(true);
    } else {
      setSubmitReady(false);
    }
  }, [selectedDeps, selectedSemester]);

  const semesters = Array.from(Array(9).keys()).map((i) => {
    i++;
    if (i === 9) {
      return {
        key: i,
        value: i,
        text: '8+ Dnem',
      };
    } else {
      return {
        key: i,
        value: i,
        text: `${i}. Dnem`,
      };
    }
  });

  const departments = [
    'Bat Dilleri ve Edebiyatlar',
    'Bilgisayar Mhendislii',
    'Bilgisayar ve retim Teknolojileri retmenlii',
    'eviribilim',
    'Dilbilim',
    'Ekonomi',
    'Elektrik Elektronik Mhendislii',
    'Endstri Mhendislii',
    'Felsefe',
    'Fen Bilgisi retmenlii',
    'Fizik',
    'Fizik retmenlii',
    'lkretim Matematik retmenlii',
    'ngilizce retmenlii',
    'naat Mhendislii',
    'letme',
    'Kimya',
    'Kimya retmenlii',
    'Makina Mhendislii',
    'Matematik',
    'Matematik retmenlii',
    'Molekler Biyololik ve Genetik',
    'Okul ncesi retmenlii',
    'Psikoloji',
    'Rehberlik ve Psikolojik Danmanlk',
    'Siyaset Bilimi ve Uluslararas likiler',
    'Sosyoloji',
    'Tarih',
    'Turizm letmecilii',
    'Trk Dili ve Edebiyat',
    'Uluslararas Ticaret',
    'Ynetim Biliim Sistemleri',
  ].map((dep) => {
    return {
      key: dep,
      value: dep,
      text: dep,
    };
  });

  const handleDepsChange = (e, data) => {
    setSelectedDeps(data.value);
  };

  const handleSemesterChange = (e, data) => {
    setSelectedSemester(data.value);
  };

  const handleSubmit = () => {
    const data = {
      quotaNotifications: { notification: isNotification, email: isEmail },
      departments: selectedDeps,
      semester: selectedSemester,
    };
    setIsLoading(true);
    dispatch(updateDepSemInfo(data, setIsLoading, setIsOpen));
  };
  return (
    <>
      <Modal open={isOpen}>
        <Modal.Header>
          <Label color="blue">
            Kota Takip in Baz Bilgilere htiyacmz Var
          </Label>
        </Modal.Header>
        <Modal.Content>
          <Modal.Description>
            ou dersi alabilmek iin belirli bir dnemde ya da blmde olmanz
            gerekiyor. Sadece sizin alabileceiniz yerler ald zaman
            bildirim alabilmeniz iin ltfen bilgileri doru doldurun.{' '}
            <Label color="red" bold>
              Eer AP yapyorsanz AP yaptnz blm de ekleyin.
            </Label>
            <br />
            <br />
            <Form reply style={{ marginBottom: '1em', marginLeft: '1em' }}>
              <Form.Field>
                <Dropdown
                  placeholder="Blm"
                  selection
                  onChange={handleDepsChange}
                  multiple
                  options={departments}
                />
              </Form.Field>

              <Form.Field>
                <Dropdown
                  placeholder="Dnem"
                  clearable
                  selection
                  onChange={handleSemesterChange}
                  options={semesters}
                />
              </Form.Field>
              <Divider />
              <Label color="blue" bold>
                Uygun Kota Varsa(Henz Aktif Deil):
              </Label>
              <br />
              <br />
              <Form.Field>
                <Checkbox
                  label="Bildirim Yolla"
                  disabled={isLoading}
                  checked={isNotification}
                  value={isNotification}
                  onChange={() => setIsNotification(!isNotification)}
                />
              </Form.Field>
              <Form.Field>
                <Checkbox
                  label="Eposta Yolla"
                  disabled={isLoading}
                  value={isEmail}
                  onChange={() => setIsEmail(!isEmail)}
                />
              </Form.Field>
            </Form>
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          <Button
            disabled={isLoading}
            negative
            content="ptal"
            icon="cancel"
            labelPosition="right"
            onClick={() => setIsOpen(false)}
          />
          <Button
            disabled={!submitReady}
            loading={isLoading}
            positive
            labelPosition="right"
            primary
            icon="checkmark"
            content="Yolla"
            onClick={() => handleSubmit()}
          />{' '}
        </Modal.Actions>
      </Modal>
    </>
  );
};

export default UserQuotaPopup;
